import React from "react";

import "./TodoList.css";

export const TodoList = (props) => {
  return (
    <div>
      <ul className="list">
        {props.todos.map((todo) => (
          <li className="listElement" key={todo.id}>
            {todo.name}
            <button
              className="delete-btn"
              onClick={() => props.deleteTask(todo.id)}
            >
              DELETE
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};
